import {request} from "./request";

// 
export function getOrderListRequest(param) {
  return request({
    url: '/orders',
    param
  })
}

// 
export function getOrderDetailRequest(id) {
  return request({
    url: `/orders/${id}`
  })
}

// 
export function editOrderRequest(id, data) {
  return request({
    url: `/orders/${id}`,
    method: 'put',
    data
  })
}
